.App {
  text-align: center;
}

.wrapper {

}

.container {
  max-width: 1100px;
  padding: 0 15px;
  margin: 0 auto;
}

@media (max-width: 1112px) {
  .container {
    max-width: 970px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 750px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: none;
    padding: 0 10px;
  }
}







